<template>
  <div class="bank container">
    <h2 class= "page-header">Banks of Nepal</h2>
    <div class="textbox">
      <input type="text" v-model="currentBank.nameofbank" placeholder="Type Bank Name" v-on:keyup.enter="getPostData()"/>
    <button type="button" class="btn btn-primary" v-on:click="getPostData()">
      <span class="glyphicon glyphicon-search"></span> Search
    </button>
    <h4>Total Results Found: {{ totalresult }} </h4>
    </div>
    <div class="table-responsive">
    <table class="table table-bordered table-hover table-striped" id="bank">
      <thead class="table-primary">
        <tr>
          <th>SN</th>
          <th>Bank Name</th>
          <th>Branch Name</th>
          <th>Address</th>
          <th>Contact</th>
          <th>Branch Manager</th>
          <th>Location</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in banklist" v-bind:key="item.id">
          <td>{{ index+currentPage*10-9 }}.</td>
          <td>{{ item.bankname }}</td>
          <td>{{ item.branchname }}</td>
          <td>{{ item.branchaddress }}</td>
          <td>{{ item.branchcontact }}</td>
          <td>{{ item.branchmanager }}</td>
          <td><b-button variant="btn btn-primary" @click="myFunction(item.mapurl)"> View Map</b-button></td>
        </tr>
      </tbody>
    </table>
    </div>
    <div class="text-center"> Showing page {{currentPage}} of {{totalcount}}</div>
    <div class="text-center">
      <b-pagination size="md" :total-rows="totalcount*10" v-model="currentPage" :per-page="10" @input="getPostData(currentPage)">
    </b-pagination>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'banklist',
  data () {
    return {
      currentPage: 1,
      currentBank: {
        nameofbank: ''
      },
      totalcount: '',
      totalresult: '',
      banklist: []
    }
  },
  methods: {
    getPostData (currentPage, currentBank) {
      axios.get('http://dkishordev.pythonanywhere.com/api/v1/banks/' + this.currentBank.nameofbank + '/' + this.currentPage)
        .then(response => {
          this.banklist = response.data.result
          this.totalcount = response.data.totalpage
          this.totalresult = response.data.resultcount
        })
        .catch(e => {
          this.errors.push(e)
        })
    },
    myFunction: function (mapurl) {
      window.open(mapurl, '_blank')
    }
  },
  mounted (currentPage, currentBank) {
    this.getPostData(currentPage, currentBank)
  },
  created: function () {
    if (this.$route.query.alert) {
      this.alert = this.$route.query.alert
    }
    this.fetchBanklist()
  }
}
</script>
<style scoped src="@/assets/styles/style.css">
</style>



