// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import Banklist from './components/Banklist'
import About from './components/About'
import Atmlist from './components/Atmlist'
import VTooltip from 'v-tooltip'
import axios from 'axios'
import BootstrapVue from 'bootstrap-vue'
import { Pagination } from 'bootstrap-vue/es/components'

Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VueRouter)
Vue.use(VTooltip)
Vue.use(axios)
Vue.use(BootstrapVue)
Vue.use(Pagination)
const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    {path: '/', component: Banklist},
    {path: '/atms', component: Atmlist},
    {path: '/about', component: About}
  ]
})
require('./assets/styles/style.css')

/* eslint-disable no-new */
new Vue({
  router,
  template: `
  <div id="app">
  <div class="nav">

      <ul>
      <li class="home"><router-link to="/">Bank </router-link></li>
      <li class="atm"><router-link to="/atms">ATMS </router-link></li>
      <li class="about"><router-link to="/about">About </router-link></li>
      </ul>
    </div>
</nav>
  <router-view></router-view>
  </div>
  
  `
}).$mount('#app')



